<template>
<el-card>
    <el-form ref="form" :model="form" :rules="rules">
        <el-form-item :label="parent.$t('bill.withdrawalCount')" prop="balance">
            <el-input v-model.number="form.balance" @change="change"></el-input>
        </el-form-item>
        <el-form-item :label="parent.$t('bill.withdrawalLimit')">
            <span>${{ accountBalance }}</span>
        </el-form-item>
    </el-form>
</el-card>
</template>

<script>
import store from '@/store/index.js';
export default {
    props: ['parent'],
    data() {
        return {
            form: {
                balance: null,
            },
            accountBalance: store.state.user.loginData.balance,
            rules: {
                balance: [{
                    type: 'number',
                    trigger: 'change',
                    min: 0,
                    max: store.state.user.loginData.balance,
                }, ],
            },
        };
    },
    methods: {
        change(value) {
            this.$emit('changeValue', value);
        },
    },
};
</script>

<style lang="scss" scoped></style>
