<template>
<div class="withdrawal_card">
    <div class="withdrawal">
        <div class="withdrawal_title">{{ $t('bill.balance') }} ($)</div>
        <div class="balance_price">{{ balance }}</div>
        <el-button type="primary" plain class="withdrawal_btn" @click="withdrawal">{{ $t('btn.withdrawal') }}</el-button>
        <withdrawal v-if="isShow"></withdrawal>
    </div>
</div>
</template>

<script>
import {
    mapActions,
    mapMutations,
    mapState
} from 'vuex';
import withdrawal from '@/components/withdrawal';
export default {
    data() {
        return {
            withdrawalPrice: '',
            isShow: false,
        };
    },
    methods: {
        withdrawal() {
            const h = this.$createElement;
            this.$msgbox({
                title: 'ID',
                message: h(withdrawal, {
                    on: {
                        changeValue: this.updateWithdrawal,
                    },
                    props: {
                        parent: this,
                    },
                }),
                confirmButtonText: this.$t('btn.confirm'),
            }).then(() => {
                if (this.withdrawalPrice > 0 && this.withdrawalPrice <= this.balance) {
                    setTimeout(() => {
                        this.$alert(this.$t('bill.submit'), '', {
                            confirmButtonText: this.$t('btn.ok'),
                        }).then(() => {
                            this.changeBalance(this.withdrawalPrice);
                        });
                    }, 1000);
                }
                let obj = {
                    shop_id: this.shopId,
                    withdrawal_time: new Date(),
                    shop_name: this.shopName,
                    account: this.account,
                    fee: this.withdrawalPrice,
                };
                this.getWithdrawal(obj);
            });
        },
        updateWithdrawal(value) {
            this.withdrawalPrice = value;
        },
        ...mapActions({
            getWithdrawal: 'bill/sWithdrawal',
        }),
        ...mapMutations({
            changeBalance: 'user/CHANGE_BALANCE',
        }),
    },
    computed: {
        ...mapState({
            balance: (state) => state.user.loginData.balance,
            shopId: (state) => state.user.data.role_id,
            account: (state) => state.user.loginData.account,
            shopName: (state) => state.user.loginData.name,
        }),
    },
    components: {
        withdrawal,
    },
};
</script>

<style lang="scss" scoped>
.withdrawal_card {
    display: flex;
    justify-content: center;
}

.withdrawal {
    text-align: center;

    .withdrawal_title {
        color: $--color-primary;
        padding: 2em 0;
    }

    .balance_price {
        padding-bottom: 2em;
        font-size: 18px;
        font-weight: bold;
    }
}
</style>
